		getSelectDataInfo(item){
			//debugger
			switch (this.currentSelect){
				case 'carNoList':
					this.formItem.carGoodsNo=item.ct_Desc
					this.getCardBoardNoInfo()
					break;
				default:
					break;
			}
		},
