			this.toShowModal =false
			switch (this.currentAction){
				case 'preSubmitDataList':
				     this.submitAddList()
					break;	
				case 'deleteDetailItem':
					this.deleteDetailItem(this.currentSelectItem.ap_OrderNo)
					break;				
				default:
