			this.toShowModal =true
		},
		// 提交添加卡板工单信息列表
		submitAddList(){
		  let params = {
			   newStationNo:this.formItem.kuNo,
			   StationNo:this.cardBoardNoInfoData[0].ap_StationNo
		  }
		  this.$store.dispatch('setStationNoAction',params).then(res=>{
			  // 清空数据
			  this.formItem = Object.assign({},defalutformItem)
